.squiggly-arrow {
  grid-template-rows: auto 1fr auto 1fr auto;
  grid-template-columns: auto 1fr auto;
  width: 100%;
  height: 100%;
  display: grid;
}

.squiggly-arrow img {
  contain: size;
}

.squiggly-arrow img:first-child {
  object-fit: none;
  object-position: 1px 0px;
  grid-area: 1 / 1 / 1 / 1;
  width: 6px;
  height: 2px;
}

.squiggly-arrow img:nth-child(2) {
  object-fit: none;
  object-position: 1px -2px;
  grid-area: 2 / 1 / 2 / 1;
  align-self: stretch;
  width: 6px;
}

